<script>
import title from "../components/title.vue";
function formatter(number) {
  const numbers = number.toString().split("").reverse();
  const segs = [];
  while (numbers.length) segs.push(numbers.splice(0, 3).join(""));
  return segs.join(",").split("").reverse().join("");
}
export default {
  data: () => {
    return {
      overlay: false, //弹窗
      config: {
        //水费数字
        number: [123456],
        content: "{nt}￥",
        formatter,
      }, // 用水量图表
      loadding: false, //加载标识
      list: {
        data: [
          //商铺水费列表
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
        ],
      },
      projectList: {
        //弹窗水费数据
        data: [
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
        ],
        rowNum: 3,
        oddRowBGC: "#122A56",
        evenRowBGC: "#1D4282",
      },
    };
  },
  components: {
    "v-title": title,
  },
  created: function () {
  },

  mounted: function () {
    this.drawLine();
  },

  methods: {
    // 打开弹窗渲染数据
    getProjectWater() {
      this.overlay = !this.overlay;
      setTimeout(() => {
        let projectWaterChart = this.$echarts.init(
          document.getElementById("projectWaterChart")
        );
        projectWaterChart.setOption({
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
              label: {
                backgroundColor: "#6a7985",
              },
            },
          },
          grid: {
            left: "0%",
            bottom: "10%",
            containLabel: true,
          },
          xAxis: [
            {
              type: "category",
              boundaryGap: false,
              data: [
                "1月",
                "2月",
                "3月",
                "4月",
                "5月",
                "6月",
                "7月",
                "8月",
                "9月",
                "10月",
                "11月",
                "12月",
              ],
              axisLabel: {
                color: "#0CEBFF",
              },
            },
          ],
          yAxis: [
            {
              type: "value",
              axisLabel: {
                color: "#0CEBFF",
              },
              splitLine: {
                show: true,
                lineStyle: {
                  color: "#34496F",
                  type: "dashed",
                },
              },
            },
          ],
          series: [
            {
              name: "月均电费",
              type: "line",
              stack: "总量",
              areaStyle: {
                color: "#52b3d9",
              },
              emphasis: {
                focus: "series",
              },
              data: [120, 132, 101, 134, 90, 230, 210, 32, 321, 544, 657, 87],
              smooth: true,
            },
          ],
        });
      }, 0);
    },
    // 绘制echarts数据
    drawLine() {
      //  月用水量面积图
      let waterChart = this.$echarts.init(
        document.getElementById("waterChart")
      );
      waterChart.setOption({
        color: ["#3ABBF2", "#8C30AD"],
        title: {
          subtext: "累计金额（元）",
          subtextStyle: {
            color: "#fff",
            fontSize: 14,
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
            splitLine: {
              show: true,
              lineStyle: {
                color: "rgba(98, 217, 255, .3)",
              },
            },
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              color: "#fff",
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "rgba(98, 217, 255, .3)",
              },
            },
          },
        ],
        series: [
          {
            name: "用水量",
            type: "line",
            stack: "总量",
            smooth: true,
            lineStyle: {
              width: 0,
            },
            showSymbol: false,
            areaStyle: {
              opacity: 1,
            },
            emphasis: {
              focus: "series",
            },
            data: [140, 232, 101, 264, 90, 340, 250, 342, 54, 768, 43, 87],
          },
          {
            name: "累计金额",
            type: "line",
            stack: "总量",
            smooth: true,
            lineStyle: {
              width: 0,
            },
            showSymbol: false,
            areaStyle: {
              opacity: 1,
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 282, 111, 234, 220, 340, 310, 132, 76, 432, 87, 43],
          },
        ],
      });

      // 公共用水柱状图 projectWaterChart
      let publicChart = this.$echarts.init(
        document.getElementById("publicChart")
      );
      publicChart.setOption({
        grid: {
          left: "0%",
          bottom: "0%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
            label: {
              backgroundColor: "#6a7985",
            },
          },
          formatter: "{b}<br />{c}t",
        },
        xAxis: {
          type: "category",
          data: ["1F", "2F", "3F", "4F"],
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(98, 217, 255, .3)'
            },
          },
          axisLabel: {
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            color: "#fff",
          },
          splitLine: {
            show: true,
            lineStyle: {
               color: 'rgba(98, 217, 255, .3)'
            },
          },
        },
        series: [
          {
            data: [120, 200, 150, 80],
            type: "bar",
            itemStyle: {
              color: "#3EC1EF",
            },
            label: {
              show: true,
              position: "top",
              color: "#fff",
              formatter: "{c}吨",
            },
          },
        ],
      });
    },
  },
};
</script>

